<template>
  <div id="app">
    <div class="roundSelect">
      <i
        v-bind:class="{ hidden: this.thisRound == 1 }"
        v-on:click="roundDown"
        class="fas fa-arrow-circle-left nav-arrow"
      ></i>
      <div>{{ getRoundInfo() }}</div>
      <i
        v-bind:class="{
          hidden:
            (thisRound == 5 && bracket == 'champ') ||
            (thisRound == 4 && bracket == 'cons') ||
            (thisRound == 2 && bracket == 'feces')
        }"
        v-on:click="roundUp"
        class="fas fa-arrow-circle-right nav-arrow"
      ></i>
    </div>
    <Bracket v-if="this.bracket == 'champ'" v-bind:thisRound="thisRound" />
    <Feces v-if="this.bracket == 'feces'" v-bind:thisRound="thisRound" />
    <Consolation v-if="this.bracket == 'cons'" v-bind:thisRound="thisRound" />

    <!-- Bottom Nav Bar for changing bracket that is displayed -->
    <nav class="navLinksContainer">
      <div class="navLink" v-on:click="changeBracket('champ')">
        <i style="color: goldenrod" class="fas fa-trophy"></i>
        <p>CHAMPIONSHIP</p>
      </div>
      <div class="navLink" v-on:click="changeBracket('cons')">
        <i style="color: lightblue" class="fas fa-award"></i>
        <p>CONSOLATION</p>
      </div>
      <div class="navLink" v-on:click="changeBracket('feces')">
        <i style="color: saddlebrown" class="fas fa-poop"></i>
        <p>FECES CUP</p>
      </div>
    </nav>
  </div>
</template>

<script>
import Bracket from "./components/Bracket.vue";
import Feces from "./components/Feces.vue";
import Consolation from "./components/Consolation.vue";

export default {
  name: "app",
  components: {
    Bracket,
    Feces,
    Consolation
  },
  data() {
    return {
      thisRound: 4,
      bracket: "champ",
      thisWeek: 15
    };
  },
  methods: {
    roundUp: function() {
      if (this.bracket == "champ" && this.thisRound == 5) {
        return;
      } else if (this.bracket == "cons" && this.thisRound == 4) {
        return;
      } else if (this.bracket == "feces" && this.thisRound == 2) {
        return;
      } else {
        this.$set(this.thisRound++);
      }
    },
    roundDown: function() {
      if (this.thisRound != 1) {
        this.$set(this.thisRound--);
      }
    },
    getRoundInfo: function() {
      if (this.bracket == "champ") {
        return (
          "Round " + this.thisRound + " • Week " + Number(this.thisRound + 11)
        );
      }
      if (this.bracket == "cons") {
        return (
          "Round " + this.thisRound + " • Week " + Number(this.thisRound + 13)
        );
      }
      if (this.bracket == "feces" && this.thisRound == 1) {
        return "Round 1 • Weeks 12-13";
      }
      if (this.bracket == "feces" && this.thisRound == 2) {
        return "Round 2 • Weeks 14-16";
      }
    },
    changeBracket: function(newBracket) {
      if (this.thisWeek == 12) {
        if (newBracket == "champ") this.thisRound = 1;
        else if (newBracket == "feces") this.thisRound = 1;
        else if (newBracket == "cons") this.thisRound = 1;
      } else if (this.thisWeek == 13) {
        if (newBracket == "champ") this.thisRound = 2;
        else if (newBracket == "feces") this.thisRound = 1;
        else if (newBracket == "cons") this.thisRound = 1;
      } else if (this.thisWeek == 14) {
        if (newBracket == "champ") this.thisRound = 3;
        else if (newBracket == "feces") this.thisRound = 2;
        else if (newBracket == "cons") this.thisRound = 1;
      } else if (this.thisWeek == 15) {
        if (newBracket == "champ") this.thisRound = 4;
        else if (newBracket == "feces") this.thisRound = 2;
        else if (newBracket == "cons") this.thisRound = 2;
      } else if (this.thisWeek == 16) {
        if (newBracket == "champ") this.thisRound = 5;
        else if (newBracket == "feces") this.thisRound = 2;
        else if (newBracket == "cons") this.thisRound = 3;
      } else if (this.thisWeek == 17) {
        if (newBracket == "champ") this.thisRound = 5;
        else if (newBracket == "feces") this.thisRound = 2;
        else if (newBracket == "cons") this.thisRound = 4;
      } else this.thisRound = 1;
      this.bracket = newBracket;
    }
  }
};
</script>

